import {autoDetectRenderer, Container, Texture, Sprite, Graphics} from 'pixi.js'
import flowerTopImg from './assets/flowerTop.png'
import './assets/style.css'

var renderer = autoDetectRenderer({width: 800, height: 600, backgroundColor: 0x1099bb});
document.getElementById('Box').appendChild(renderer.view);

var stage = new Container();
var texture1 = Texture.fromImage(flowerTopImg);
var texture2 = Texture.fromImage('./static/assets/bunny.png')
var tnum = 5
console.log("render Object Number:", tnum);
var bunnys = [];
for (var i = 0; i < tnum; i++) {
  var bunny = new Sprite(Math.random() <= 0.5 ? texture1 : texture2);
  bunny.position.x = Math.random() * 800;
  bunny.position.y = Math.random() * 600;
  bunny.anchor.set(0.5, 0.5)
  stage.addChild(bunny);
  bunnys.push(bunny);
}

var graphics = new Graphics();
// Rectangle
graphics.beginFill(0xDE3249);
graphics.drawRect(150, 150, 100, 100);
graphics.endFill();
stage.addChild(graphics);

animate();

function animate() {
  requestAnimationFrame(animate);

  for (var i = 0; i < tnum; i++) {
    bunnys[i].rotation += 0.1;
  }
  renderer.render(stage);
}

